.containerParent {
  display: grid;
  grid-template-areas: ". container .";
  grid-template-columns: 7% 86% 7%;
}
.containerChild {
  grid-area: container;
}
body .header .container .header__nav-panel__goods .dropMenu {
  width: 60%;
  position: absolute;
  z-index: 10;
  padding: 0 25px 25px 0;
  display: flex;
  flex-wrap: wrap;
  background-color: #9e79ac;
}
body .header .container .header__nav-panel__goods .dropMenu li {
  margin: 20px 0 0 25px;
  width: 30%;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
}
body .header .container .header__nav-panel__goods .dropMenu li a {
  color: #fff;
  text-align: left;
  border: none;
  background-color: #9e79ac;
}
body .header .container .header__nav-panel__goods .dropMenu li a:hover {
  text-decoration: underline;
}
body .header .container .header__nav-panel__goods .dropMenu li:nth-child(1) {
  font: 1em/1.125em "PTSans-Bold";
}
body .header .container .header__nav-panel__goods .dropMenu li:nth-child(1) i {
  line-height: 1.125em;
}
body .header .container .header__nav-panel__goods .dropMenu li ul {
  display: flex;
  flex-direction: column;
}
body .header .container .header__nav-panel__goods .dropMenu li ul li {
  margin: 0 0 0 25px;
  width: 100%;
}
body .header .container .header__nav-panel__goods .dropMenu li ul li a {
  font: 1em/1.125em "PTSans-Regular";
}
@media screen and (max-width: 740px), screen and (max-device-width: 740px) {
  body .header .container .header__nav-panel__goods .dropMenu {
    width: 60%;
    top: 0;
    left: 300px;
  }
  body .header .container .header__nav-panel__goods .dropMenu li {
    position: relative;
    margin: 5px 0 0 25px;
    width: 100%;
  }
  body .header .container .header__nav-panel__goods .dropMenu li ul {
    display: none;
  }
  body .header .container .header__nav-panel__goods .dropMenu li:hover ul {
    display: block;
  }
  body .header .container .header__nav-panel__goods .dropMenu li:hover ul li {
    display: none;
  }
}
body .header {
  width: 100%;
  position: absolute;
  top: 0;
  display: grid;
  grid-template-areas: ". container .";
  grid-template-columns: 7% 86% 7%;
}
body .header .container {
  grid-area: container;
  display: grid;
  grid-template-areas: ". headline headline" "logo search cabinet" "navPanel navPanel navPanel" "secondNav secondNav secondNav";
  grid-template-columns: 25% 50% 25%;
}
body .header .container .header__headline {
  grid-area: headline;
}
body .header .container .header__headline p {
  font: 1em/2em "PTSans-Regular";
  color: #474747;
  text-align: center;
  background-color: #ececec;
  border-bottom-right-radius: 15px;
  border-bottom-left-radius: 15px;
}
body .header .container .header__logo {
  grid-area: logo;
}
body .header .container .header__logo a {
  margin-left: 50px;
  display: block;
  width: 100%;
  height: 100%;
  background: url("../img/logo.png") no-repeat;
  background-size: contain;
}
body .header .container .header__mainSearch {
  grid-area: search;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}
body .header .container .header__mainSearch .search {
  width: 100%;
  margin-top: 70px;
  text-align: center;
}
body .header .container .header__mainSearch .search input {
  width: 90%;
  height: 45px;
  padding-left: 25px;
  font: 1.125em/45px "PTSans-Regular";
  color: #9e9e9e;
  border: 3px solid #00929e;
  border-radius: 50px;
  outline: none;
}
body .header .container .header__mainSearch .search i {
  font-size: 1.5em;
  position: relative;
  left: -50px;
  top: 2px;
  color: #00929e;
}
body .header .container .header__mainSearch .numbers {
  margin-top: 50px;
  display: flex;
  align-items: center;
}
body .header .container .header__mainSearch .numbers i {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 65px;
  height: 65px;
  font-size: 2em;
  color: #00929e;
  border: 3px solid #00929e;
  border-radius: 50px;
}
body .header .container .header__mainSearch .numbers ul {
  display: flex;
}
body .header .container .header__mainSearch .numbers ul li {
  padding: 0 10px;
}
body .header .container .header__mainSearch .numbers ul li a {
  font: 1.6em/2em "PTSans-Regular";
  color: #474747;
  text-decoration: none;
}
body .header .container .header__mainSearch .workTime {
  margin: 55px 20px 0 60px;
  display: flex;
}
body .header .container .header__mainSearch .workTime i {
  font-size: 1.5em;
  line-height: 1.725em;
  color: #00929e;
}
body .header .container .header__mainSearch .workTime ul {
  margin-left: 20px;
}
body .header .container .header__mainSearch .workTime ul li span {
  font: 0.7em/1em "PTSans-Regular";
}
body .header .container .header__cabinet {
  grid-area: cabinet;
  margin-top: 70px;
}
body .header .container .header__cabinet ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
body .header .container .header__cabinet ul li a {
  display: flex;
  justify-content: space-around;
  align-items: center;
  text-decoration: none;
}
body .header .container .header__cabinet ul li a span {
  display: inline-block;
  font: 1.125em/1.1em "PTSans-Regular";
  color: #474747;
}
body .header .container .header__cabinet ul li a span:nth-child(2) {
  width: 100%;
  text-align: center;
}
body .header .container .header__cabinet ul li a span i {
  padding: 0 20px;
  font-size: 2em;
  color: #00929e;
}
body .header .container .header__cabinet ul li a span:hover {
  text-decoration: underline;
  cursor: pointer;
}
body .header .container .header__cabinet ul li:nth-child(1) {
  width: 45%;
}
body .header .container .header__cabinet ul li:nth-child(2) {
  width: 45%;
}
body .header .container .header__cabinet ul li:nth-child(3) a {
  margin-top: 50px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font: 1.125em/1.1em "PTSans-Regular";
  color: #474747;
  text-align: center;
  text-decoration: none;
  border: 1px solid #00929e;
  border-radius: 5px;
}
body .header .container .header__cabinet ul li:nth-child(3) a span {
  margin: 5px 10px;
}
body .header .container .header__cabinet ul li:nth-child(3) a i {
  padding: 3px;
  font-size: 2em;
  color: #fff;
  background-color: #7d3daf;
  border-radius: 5px;
}
body .header .container .header__nav-panel {
  grid-area: navPanel;
  margin-top: 50px;
  display: flex;
  flex-direction: column;
}
body .header .container .header__nav-panel .mobileDrop {
  display: none;
}
body .header .container .header__nav-panel__major ul {
  display: flex;
  justify-content: space-between;
}
body .header .container .header__nav-panel__major ul li {
  width: 16.67%;
}
body .header .container .header__nav-panel__major ul li a {
  display: inline-block;
  position: relative;
  width: 100%;
  font: 1.125em/2em "PTSans-Bold";
  color: #fff;
  text-align: center;
  text-decoration: none;
  background-color: #176975;
  border: 1px solid #77c9cc;
}
body .header .container .header__nav-panel__major ul li a:hover {
  transition: .5s;
  background-color: #9e79ac;
}
body .header .container .header__nav-panel__major ul .footer {
  display: none;
  height: 100vh;
  background-color: #176975;
  border: 1px solid #77c9cc;
}
body .header .container .header__nav-panel__goods ul {
  display: flex;
  justify-content: space-between;
}
body .header .container .header__nav-panel__goods ul li {
  width: 25%;
}
body .header .container .header__nav-panel__goods ul li a {
  display: inline-block;
  width: 100%;
  font: 1.125em/2em "PTSans-Regular";
  color: #0e3a40;
  text-align: center;
  text-decoration: none;
  background-color: #77c9cc;
  border: 1px solid #176975;
}
body .header .container .header__nav-panel__goods ul li a:hover {
  color: #fff;
  background-color: #9e79ac;
  transition: .5s;
}
@media screen and (max-width: 1400px), screen and (max-device-width: 1400px) {
  body {
    font-size: 16px;
    line-height: 18px;
  }
  body .header .container .header__mainSearch .search {
    margin-top: 40px;
  }
  body .header .container .header__mainSearch .numbers {
    margin-top: 35px;
  }
  body .header .container .header__mainSearch .numbers ul li i {
    width: 45px;
    height: 45px;
  }
  body .header .container .header__mainSearch .workTime {
    margin: 40px 0 0 15px;
  }
  body .header .container .header__cabinet {
    margin-top: 40px;
  }
  body .header .container .header__cabinet ul li:nth-child(3) a i {
    margin: 0 !important;
  }
}
@media screen and (max-width: 1310px), screen and (max-device-width: 1310px) {
  body .header .container .header__logo a {
    margin-left: 0;
  }
  body .header .container .header__mainSearch .search input {
    height: 30px;
    line-height: 30px;
  }
  body .header .container .header__mainSearch .search i {
    font-size: 1em;
  }
  body .header .container .header__mainSearch .numbers i {
    width: 30px;
    height: 30px;
    font-size: 1.5em;
  }
  body .header .container .header__mainSearch .workTime {
    margin: 40px 20px 0 10px;
  }
  body .header .container .header__mainSearch .workTime ul {
    margin-left: 10px;
  }
  body .header .container .header__mainSearch .workTime ul li span {
    font-size: 1em;
    line-height: 1.125em;
  }
  body .header .container .header__cabinet ul li a span i {
    padding: 0 10px;
  }
}
@media screen and (max-width: 1050px), screen and (max-device-width: 1050px) {
  body .header .container {
    grid-template-areas: "headline headline headline" "logo search cabinet" "navPanel navPanel navPanel" "secondNav secondNav secondNav";
  }
  body .header .container .header__logo {
    margin-top: 20px;
  }
  body .header .container .header__mainSearch .search {
    margin-top: 20px;
  }
  body .header .container .header__mainSearch .search input {
    width: 80%;
  }
  body .header .container .header__mainSearch .numbers {
    margin-top: 0;
  }
  body .header .container .header__mainSearch .workTime {
    margin: 20px 20px 0 10px;
  }
  body .header .container .header__cabinet {
    margin-top: 25px;
  }
  body .header .container .header__cabinet ul li a span i {
    padding: 0 5px;
  }
  body .header .container .header__cabinet ul li:nth-child(3) a span {
    margin: 5px;
  }
}
@media screen and (max-width: 1010px), screen and (max-device-width: 1010px) {
  body .header .container .header__mainSearch .numbers ul {
    flex-direction: column;
  }
  body .header .container .header__mainSearch .numbers ul li a {
    font-size: 1.375em;
    line-height: 1.5em;
  }
}
@media screen and (max-width: 780px), screen and (max-device-width: 780px) {
  body .header .container .header__cabinet ul {
    flex-direction: column;
  }
  body .header .container .header__cabinet ul li {
    margin: 5px 0;
    width: 100%!important;
  }
  body .header .container .header__cabinet ul li a {
    justify-content: unset;
  }
  body .header .container .header__cabinet ul li a span i {
    padding: 0 10px;
  }
  body .header .container .header__cabinet ul li:nth-child(3) a {
    margin-top: 0;
  }
  body .header .container .header__nav-panel {
    margin-top: 30px;
  }
  body .header .container .header__nav-panel__major ul li a,
  body .header .container .header__nav-panel__goods ul li a {
    font-size: 1em;
  }
}
@media screen and (max-width: 740px), screen and (max-device-width: 740px) {
  body .header .container .header__mainSearch {
    flex-direction: column;
  }
  body .header .container .header__mainSearch .numbers,
  body .header .container .header__mainSearch .workTime {
    margin: 10px 0 0;
    justify-content: center;
  }
  body .header .container .header__mainSearch .numbers ul,
  body .header .container .header__mainSearch .workTime ul {
    display: flex;
    flex-direction: row;
    justify-content: center;
