@import "variables";
@import "mixin";
@import "main_drop";

body{
  .header {
    width: 100%;
    position: absolute;
    top: 0;
    .containerParent();
    .container {
      .containerChild();
      display: grid;
      grid-template-areas: ". headline headline"
                           "logo search cabinet"
                           "navPanel navPanel navPanel"
                           "secondNav secondNav secondNav";
      grid-template-columns: 25% 50% 25%;
      .header__headline {
        grid-area: headline;
        p {
          font: 1em/2em @sansregular;
          color: #474747;
          text-align: center;
          background-color: #ececec;
          border-bottom-right-radius: 15px;
          border-bottom-left-radius: 15px;
        }
      }
      .header__logo {
        grid-area: logo;
        a{
          margin-left: 50px;
          display: block;
          width: 100%;
          height: 100%;
          background: url("../img/logo.png") no-repeat;
          background-size: contain;
        }
      }
      .header__mainSearch {
        grid-area: search;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        .search {
          width: 100%;
          margin-top: 70px;
          text-align: center;
          input {
            width: 90%;
            height: 45px;
            padding-left: 25px;
            font: 1.125em/45px @sansregular;
            color: #9e9e9e;
            border: 3px solid @mainColor;
            border-radius: 50px;
            outline: none;
          }
          i {
            font-size: 1.5em;
            position: relative;
            left: -50px;
            top: 2px;
            color: @mainColor;
          }
        }
        .numbers {
          margin-top: 50px;
          display: flex;
          align-items: center;
          i {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 65px;
            height: 65px;
            font-size: 2em;
            color: @mainColor;
            border: 3px solid @mainColor;
            border-radius: 50px;
          }
          ul {
            display: flex;
            li {
              padding: 0 10px;
              a {
                font: 1.6em/2em @sansregular;
                color: #474747;
                text-decoration: none;
              }
            }
          }
        }
        .workTime {
          margin: 55px 20px 0 60px;
          display: flex;
          i {
            font-size: 1.5em;
            line-height: 1.725em;
            color: @mainColor;
          }
          ul {
            margin-left: 20px;
            li {
              span {
                font: .7em/1em @sansregular;
              }
            }
          }
        }
      }
      .header__cabinet {
        grid-area: cabinet;
        margin-top: 70px;
        ul {
          display: flex;
          flex-wrap: wrap;
          justify-content: space-between;
          li {
            a {
              display: flex;
              justify-content: space-around;
              align-items: center;
              text-decoration: none;
              span {
                display: inline-block;
                font: 1.125em/1.1em @sansregular;
                color: #474747;
                &:nth-child(2){
                  width: 100%;
                  text-align: center;
                }
                i {
                  padding: 0 20px;
                  font-size: 2em;
                  color: @mainColor;
                }
                &:hover {
                  text-decoration: underline;
                  cursor: pointer;
                }
              }
            }
            &:nth-child(1) {
              width: 45%;
            }
            &:nth-child(2) {
              width: 45%;
            }
            &:nth-child(3) {
              a{
                margin-top: 50px;
                width: 100%;
                display: flex;
                justify-content: space-between;
                align-items: center;
                font: 1.125em/1.1em @sansregular;
                color: #474747;
                text-align: center;
                text-decoration: none;
                border: 1px solid @mainColor;
                border-radius: 5px;
                span{
                  margin: 5px 10px;
                }
                i{
                  padding: 3px;
                  font-size: 2em;
                  color: #fff;
                  background-color: #7d3daf;
                  border-radius: 5px;
                }
              }
            }
          }
        }
      }
      .header__nav-panel {
        grid-area: navPanel;
        margin-top: 50px;
        display: flex;
        flex-direction: column;
        .mobileDrop{
          display: none;
        }
        &__major {
          ul {
            display: flex;
            justify-content: space-between;
            li {
              width: 16.67%;
              a {
                display: inline-block;
                position: relative;
                width: 100%;
                font: 1.125em/2em @sansbold;
                color: #fff;
                text-align: center;
                text-decoration: none;
                background-color: @fontColor;
                border: 1px solid #77c9cc;
                &:hover {
                  transition: .5s;
                  background-color: @hoverColor;
                }
              }
            }
            .footer{
              display: none;
              height: 100vh;
              background-color: @fontColor;
              border: 1px solid #77c9cc;
            }
          }
        }
        &__goods {
          ul {
            display: flex;
            justify-content: space-between;
            li {
              width: 25%;
              a {
                display: inline-block;
                width: 100%;
                font: 1.125em/2em @sansregular;
                color: @linkColor;
                text-align: center;
                text-decoration: none;
                background-color: #77c9cc;
                border: 1px solid @fontColor;
                &:hover {
                  color: #fff;
                  background-color: @hoverColor;
                  transition: .5s;
                }
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1400px), screen and (max-device-width: 1400px) {
  body {
    font-size: 16px;
    line-height: 18px;
    .header {
      .container {
        .header__mainSearch {
          .search {
            margin-top: 40px;
          }
          .numbers {
            margin-top: 35px;
            ul {
              li {
                i {
                  width: 45px;
                  height: 45px;
                }
              }
            }
          }
          .workTime {
            margin: 40px 0 0 15px;
          }
        }
        .header__cabinet {
          margin-top: 40px;
          ul {
            li {
              &:nth-child(3) {
                a{
                  i {
                    margin: 0 !important;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1310px), screen and (max-device-width: 1310px){
  body{
    .header{
      .container{
        .header__logo{
          a{
            margin-left: 0;
          }
        }
        .header__mainSearch {
          .search{
            input{
              height: 30px;
              line-height: 30px;
            }
            i{
              font-size: 1em;
            }
          }
          .numbers{
            i{
              width: 30px;
              height: 30px;
              font-size: 1.5em;
            }
          }
          .workTime{
            margin: 40px 20px 0 10px;
            ul{
              margin-left: 10px;
              li{
                span{
                  font-size: 1em;
                  line-height: 1.125em;
                }
              }
            }
          }
        }
        .header__cabinet{
          ul{
            li{
              a{
                span{
                  i{
                    padding: 0 10px;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1050px), screen and (max-device-width: 1050px){
  body{
    .header{
      .container{
        grid-template-areas: "headline headline headline"
                             "logo search cabinet"
                             "navPanel navPanel navPanel"
                             "secondNav secondNav secondNav";
        .header__logo{
          margin-top: 20px;
        }
        .header__mainSearch{
          .search{
            margin-top: 20px;
            input{
              width: 80%;
            }
          }
          .numbers{
            margin-top: 0;
          }
          .workTime{
            margin: 20px 20px 0 10px;
          }
        }
        .header__cabinet{
          margin-top: 25px;
          ul{
            li{
              a{
                span{
                  i{
                    padding: 0 5px;
                  }
                }
              }
              &:nth-child(3){
                a{
                  span{
                    margin: 5px;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1010px), screen and (max-device-width: 1010px){
  body{
    .header{
      .container{
        .header__mainSearch{
          .numbers{
            ul{
              flex-direction: column;
              li{
                a{
                  font-size: 1.375em;
                  line-height: 1.5em;
                }
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 780px), screen and (max-device-width: 780px){
  body{
    .header{
      .container{
        .header__cabinet{
          ul{
            flex-direction: column;
            li{
              margin: 5px 0;
              width: 100%!important;
              a{
                justify-content: unset;
                span{
                  i{
                    padding: 0 10px;
                  }
                }
              }
              &:nth-child(3){
                a{
                  margin-top: 0;
                }
              }
            }
          }
        }
        .header__nav-panel{
          margin-top: 30px;
          &__major,
          &__goods{
            ul{
              li{
                a{
                  font-size: 1em;
                }
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 740px), screen and (max-device-width: 740px){
  body{
    .header{
      .container{
        .header__mainSearch{
          flex-direction: column;
          .numbers,
          .workTime{
            margin: 10px 0 0;
            justify-content: center;
            ul{
              display: flex;
              flex-direction: row;
              justify-content: center;
              align-items: center;
            }
          }
        }
        .header__nav-panel{
          align-items: center;
          .mobileDrop{
            display: block;
            width: 90%;
            font: 1.25em/2em @sansbold;
            color: @linkColor;
            text-align: center;
            background-color: @mainColor;
            border-radius: 5px;
            text-decoration: none;
          }
          &__major,
          &__goods{
            display: none;
            position: fixed;
            z-index: 20;
            ul{
              flex-direction: column;
              li{
                width: 300px;
                a{
                  font-size: 1.5em;
                }
              }
              .footer{
                display: block;
              }
            }
          }
          &__major{
            top: 190px;
            left: -160px;
          }
          &__goods{
            top: 0;
            left: -160px;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 680px), screen and (max-device-width: 680px){
  body{
    .header{
      .container{
        grid-template-columns: 25% 65% 10%;
        .header__cabinet{
          ul{
            li{
              a{
                justify-content: center;
                span{
                  &:nth-child(2){
                    display: none;
                  }
                }
              }
              &:nth-child(3){
                a{
                  justify-content: center;
                  border: none;
                }
              }
            }
          }
        }
      }
    }
  }
}